$ratio: 750/640;
$browser-default-font-size: 14;
$browser-default-font-size-px: 14px !default;
$lte7:                  true !default; //是否兼容ie6-7

@function r($px, $isFixed:true) {
  @if $px != 0 {
    @return if($isFixed, $px / $ratio + 0.01, $px / $ratio) / $browser-default-font-size * 1rem;
  } 
  @else
  {
    @return 0;
  }
}
$designWidth : 750; //如设计图是750
$deviceWidth : 320;//视图的宽度
@function pxToRem( $px ){
    @return $px * $deviceWidth / $designWidth / $browser-default-font-size * 1rem;; 
}


.menu-item {
  display: flex;
  padding: r(25) 0;
  border-bottom: 1px solid #f0f0f0;
  position: relative;
  .img {
    width: r(62);
    height: r(62);
    margin-right: r(10);
  }
  .menu-item-right {
    flex: 1;
  }
  .item-title {
    font-size: r(16);
    color: #2f2f2f;
  }
  .item-desc,.item-zan {
    color: #a9a9a9;
    font-size: r(12);
    margin-top: r(6);
    line-height: r(14);
    padding-right: r(4);

    &.item-desc {
      line-height: r(17);
    }
  }
  .item-price {
    margin-top: r(10);
    color: #fe4d3d;
    font-size: r(18);
  }
  .unit {
    color: #a9a9a9;
    font-size: r(12);
  }
  .select-content {
    position: absolute;
    right: r(9);
    bottom: r(21);
    display: flex;
  }

  .plus {
    width: r(25);
    height: r(25);
    background-size: cover;
    background-image: url('./img/plus.png');
    background-repeat: no-repeat;
  }

  .minus {
    width: r(25);
    height: r(25);
    background-size: cover;
    background-image: url('./img/minus.png');
    background-repeat: no-repeat;
  }

  .count {
    font-size: r(15);
    color: #2f2f2f;
    width: r(20);
    height: r(25);
    line-height: r(25);
    margin: 0 r(2);
    text-align: center;
  }
}